.section14 {
    width: 100%;
    height: 100vh;
    position: relative;
    top: -90px;
}

.sect14__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.sect14__bg img {
    width: 100%;
    height: 100%;
}

.sect14__title {
    margin: auto;
    width: 70%;
    padding-top: 35px;
}

.sect14__title img {
    width: 100%;

}

.sect14 {
    display: flex;
    justify-content: center;

}

.sect14__cards-bottom {
    position: absolute;
    bottom: 30px;
}

.sect14__text-bottom h3 {
    color: white;
    font-size: 30px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    width: 100%;


}

.sect14__input input {
    background-color: transparent;
    color: white;
    padding: 10px 120px;
    border: 1px white solid;
    border-radius: 20px;
    width: 100%;

}

.sect14__text-bottom {
    text-align: center;
}


.sect14__buy-btn {
    padding: 10px 150px;
    color: black;
    border: 1px white solid;
    border-radius: 20px;
    font-weight: 300;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    transition: 300ms ease;
    background-color: white;
}

.sect14__buy-btn:hover {
    color: white;

    background-color: transparent;
}

.sect14__bg img:nth-child(2) {
    display: none;

}


@media screen and (max-width:500px) {


    .sect14__bg img:nth-child(2) {
        display: block;

    }

    .sect14__bg img:nth-child(1) {
        display: none;

    }

    .section14 {
        height: 200vh;
        width: 100%;
    }

    .sect14__input {
        width: 100%;
    }

    .sect14__input input {
        width: 100%;

    }

    .sect14__buy-btn {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .sect14__text-bottom h3 {
        width:80%;
        margin: auto;
        font-size: 25px;
    }

    .sect14__bottum-item1 {
        padding: 10px 0px;
    }

    .sect14__bottum-item1 {
        width: 100%;
    }

    .sect14__input {
        width: 100%;
        margin: auto;
    }
    .sect14_card1{
        margin-top: 30px;
    }
    .sect14__buy-btn{
        padding: 5px;
        margin-top: 30px;
    }
    .sect14_card1 input::placeholder {

        text-align: center;
        color: white;
        font-weight: 300;
        font-size: 15px;
        font-family: 'Roboto', sans-serif;
    }


}
.sect14_card1 input::placeholder {

    text-align: center;
    color: white;
    font-weight: 300;
    font-size: 15px;
    font-family: 'Roboto', sans-serif;
}
.btn img{
    cursor: pointer;
    width: 50px;
    position: fixed;
    bottom: 50px;
    right: 50px;

}