.section13 {
    height: 440vh;
    position: relative;
    top: -90px;

}

.sect13__img {
    text-align: center;
    padding-top: 60px;

}
.sect13__card-subtitle p:nth-child(2){
    width: 50%;
}
.sect13__card-item p:nth-child(2){
    width: 50%;
}

.sect13__img {
    width: 100%;
}

.sect13__img h1 {

    font-size: 85px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
    width: 100%;
}

.sect13__img img {
    width: 100%;
    height: 100%;
}

.sect13__text1 {
    display: flex;
    align-items: center;
}

.sect13__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;

}

.sect13__bg img {
    width: 100%;
    height: 100%;

}

.sect13__box {
    padding-top: 60px;
    color: white;
}

.sect13__p1 {
    width: 50%;
}

.sect13__p1 p {
    font-weight: 300;
    font-size: 20px;

}

.sect13__p2 p {
    font-size: 20px;
    font-family: 'Roboto ', sans-serif;

}

.sect13__text2 {
    display: flex;
    align-items: center;
}

.sect13__mbbg {
    display: none;
}

.sect13__img img:nth-child(2) {
    display: none;

}













.sect13__card {
    width: 100%;
    padding: 10px 0px 0px 0px;
    background: rgb(205, 182, 167);
    background: linear-gradient(90deg, rgba(205, 182, 167, 1) 0%, rgba(180, 150, 131, 1) 11%, rgba(180, 150, 131, 1) 93%, rgba(205, 182, 167, 1) 100%);
}
.sect13__card-subtitle{
    padding: 10px 0px;
}
.sect13__card-item{
    margin: auto;
    width: 90%;
    display: flex;
    justify-content: start;
    align-items: center;
}

.sect13__card-subtitle{
    margin: auto;
    width: 90%;
    color: white;

}
.sect13__card-subtitle p:nth-child(1){
    width: 50%;

}
.sect13__card-item p:nth-child(1){
    width: 50%;

}
.sect13__card-subtitle{
    display: flex;
}
.sect13__card-item p{
    color: white;
    

}










@media screen and (max-width:900px) {
    .sect13__img h1 {

        font-size: 55px;
    }
    .section13 {
        height: 540vh;
    
    }


}

@media screen and (max-width:650px) {
    .sect13__img h1 {

        font-size: 45px;
    }


}

@media screen and (max-width:500px) {
    .sect13__mbbg {
        display: block;
    }

    .sect13__lgbg {
        display: none;
    }
    .section13 {
        height: 700vh;
    
    }

    .sect13__img img:nth-child(2) {
        display: block;

    }

    .sect13__img h1 {

        font-size: 35px;
    }

    .sect13__img img:nth-child(1) {
        display: none;

    }

}