.section11 {
    position: relative;
    top: -90px;
    width: 100%;
    height: 123.2vh;
}

.sect11__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.sect11__bg img {
    width: 100%;
    height: 100%;
}

.sect11__title {
    padding-top: 20px;

}

.sect11__box {
    display: flex;
    justify-content: end;

}

.sect11__p {
    color: #66483e;
}
.sect11__text22 h1{
    
    font-size: 70px;
    text-align: end;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
}

.sect11__text22 {
    width: 75%;
}
.sect11__bg {
    height: 100%;
}

.sect11__p p{
    width: 40%;
}
.sect11__text22 p {
    text-align: end;
    font-size: 18px;
    padding-top: 20px;
}

.sect11__p {
    display: flex;
    justify-content: end;
}

.sect11__bg-mb {
    display: none;

}

.sect11__photo2 {
    display: none;

}

@media screen and (max-width:500px) {
    .sect11__bg-mb {
        display: block;

    }
    .sect11__p{
        width: 100%;
    }
    .sect11__text22{
        width: 100%;
    }
    .sect11__bg-lg {
        display: none;

    }
    .sect11__text22 h1{
        font-size: 30px;
        font-weight: 500;

        text-align: center;

    }
    .sect11__p p{
        width: 100%;
        text-align: center;
        padding-top: 165px;
        
    }
    .sect11__box {
        width: 100%;
        justify-content: center;
    }

    .sect11__box img {
        width: 100%;
    }

    .sect11__text22 {
        width: 100%;
    }

    .sect11__text22 p {
        text-align: center;
    }

    .sect11__photo2 {
        display: block;
    }

    .sect11__photo {
        display: none;
    }

    .section11 {
        height: 140vh;
    }

    .sect11__text22 {
        width: 90%;
        margin: auto;
        padding-top: 20px;
    }
}