.section10 {
    height: 100%;
    background-color: black;
    position: relative;
    top: -90px;
    padding: 30px 0px;
}

.sect10__title {
    text-align: center;
    padding-top: 30px;
}

.sect10__title {
    margin: auto;
    width: 60%;
}

.sect10__cards {
    padding-top: 60px;
}

.sect10_btn {
    border: 0.5px white solid;
}

.sect10__title img {
    width: 100%;
}

.sect10_btn {
    transition: 300ms ease;
}

.sect10_btn:hover {
    color: white;
    background-color: black;
}

.sect10__card-btn button {
    border: none;
    transition: 300ms ease;

}

.sect10__card-btn button:hover {
    -webkit-box-shadow: 0px 5px 20px -5px rgba(255, 255, 255, 0.41);
    -moz-box-shadow: 0px 5px 20px -5px rgba(255, 255, 255, 0.41);
    box-shadow: 0px 5px 20px -5px rgba(255, 255, 255, 0.41);
    color: rgba(255, 255, 255, 0.823);
    background-color: rgb(88, 88, 88);

}

.sect10_btn {
    padding: 10px 0px;
    width: 100%;
    background-color: white;
    color: black;
    font-family: 'Roboto ', sans-serif;
    font-weight: 300;
    border-radius: 10px;
    font-size: 20px;
}

.sect10_box {
    display: block;
    align-items: center;

}

.sect10__text {
    padding-top: 25px;
    margin: auto;
    width: 80%;
    color: #d2cdca;
}

.sect10__text p {
    line-height: 30px;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    text-align: start;
    font-weight: 300;
}

.sect10__text {
    height: 400px;
}

.sect10__card-btn button {
    display: block;
    margin: auto;
    width: 100%;
    margin-top: 20px;
    background-color: #cdb6a7;
    color: white;
    font-size: 20px;
    border-radius: 10px;
    padding: 5px;
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width:700px) {

    .sect10__text p {
        font-size: 18px;
    }
    .delicotes p{
        text-align: end;
    }
    .sect10_box:nth-child(3)
    {
        margin-top: 50px;
    }
    .sect10_box:nth-child(4)
    {
        margin-top: 50px;
    }
    .sect10_box:nth-child(5)
    {
        margin-top: 50px;
        margin-bottom: 50px;
    }
    .sect10_box:nth-child(6)
    {
        margin-top: 50px;
    }
    .always p{
        text-align: end;
        font-size: 16px;
    }
    .okazivaet p{
        text-align: end;
    }
    .sect10__card-btn button{
        font-size: 18px;
    }
    .sect10_btn{
        font-size: 18px;
        padding: 6px 0px;
    }
}