.section6 {
    height: 134vh;
    position: relative;
    z-index: -2;
    background-color: black;
}

.sect6__bg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: -51px;

}
.sect6__bg img {
    width: 100%;
    height: 100%;
}
.sect6__texts img {
    width: 100%;
}

.sect6__texts {
    width: 80%;
    margin: auto;
}

.sect6__bottom-text {
    position: absolute;
    bottom: 100px;
    right: 100px;
    width: 42%;
}

.sect6__bottom-text p {
    text-align: end;
    color: white;
    font-size: 25px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
}

.sect6__texts p {

    font-size: 40px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
}

.sect6__mbbg {
    display: none;
}

.sect6__texts img:nth-child(2) {
    display: none;

}

.sect6__texts img:nth-child(3) {
    display: none;
}

.sect6__texts h1 {

    font-size: 80px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
}

.sect6__texts {
    width: 100%;
}

@media screen and (max-width:1000px) {
    .sect6__texts h1 {
        font-size: 45px;
    }

    .sect6__bottom-text p {
        font-size: 15px;
    }

    .sect6__texts p {
        font-size: 20px;
    }


}

@media screen and (max-width:500px) {

    .sect6__mbbg {
        display: block;
    }

    .sect6__texts h1 {
        font-size: 30px;
    }
    .sect6__texts p{
        display: none;
    }

    .sect6__lgbg {
        display: none;
    }

    .sect6__texts img:nth-child(1) {
        display: none;

    }

    .sect6__texts img:nth-child(2) {
        display: block;

    }

    .sect6__bottom-text p {
        text-align: center;
        font-size: 15px;
        width: 100%;

    }

    .sect6__bottom-text {
        width: 100%;
        display: flex;
        justify-content: center;
        right: auto;

    }

    .sect6__texts h1 {

        text-align: center;
        background: rgb(255, 233, 176);
        background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
        background-clip: text;
        color: transparent;
        font-family: 'Open Sans', sans-serif;
        font-weight: 500;
        text-transform: uppercase;
    }

    .sect6__bottom-cards {
        display: flex;
        justify-content: center;
    }

    .sect6__bottom-text {
        width: 65%;
    }

    .sect6__texts img:nth-child(3) {
        display: block;
        padding-top: 30px;
    }
}