@media screen and (max-width:990px) {

    .Phantom__textphoto {
        display: none;
    }

    .Kreslo {
        display: block;
        width: 100%;
    }

    .Phantom__title {
        padding-top: 30px;
        display: flex;
        justify-content: space-between;
    }

    .Kreslo__Box1 {
        margin: auto;
        width: 100%;
    }

    .Kreslo__box1small-photo {
        width: 100%;
    }

    .Kreslo__box2 {
        width: 100%;
        margin: auto;
    }

    .Section2__flagkoreya {
        display: flex;
        justify-content: space-between;
    }

    .Phantom__textend {
        height: 100%;
        width: 100%;
    }

    .Kreslo__box1-photo {
        width: 100%;
    }

    .Kreslo__box1small-photo {
        width: 100%;
    }
    .Kreslo__skidka{
        display: none;
    }
    .Kreslo__box1small-photo img {
        width: 30%;
        text-align: center;
    }
    .Kreslo__text-massas{
        font-size: 20px;
    }
    .Kreslo__text-roliki{
        font-size: 12px;
    }
    .Kreslo_text-price{
        font-size: 30px;
    }
    .Kreslo__box1small-photo{
        padding: 0;
    }
    .Phantom__textend h2{
        font-size: 20px;
    }
    .premial{
        font-size: 15px;
    }
    .koreya{
        font-size: 16px;
    }
}