.section4 {
    height: 130vh;
    position: relative;
    top: -31px;
}

.sect4__bg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.sect2__text-lev {
    margin: auto;
    width: 70%;
}

.sect4__bg img {
    width: 100%;
    height: 100%;
}

.Sect4 {
    height: 130vh;
}

.sect2__bottom-title img {
    width: 100%;
}

.Sect4__title-lev {
    text-align: center;
    padding-top: 20px;
}

.sect2__bottom-title p {
    color: white;
    padding-top: 20px;
    margin: auto;
    width: 95%;
    font-size: 30px;

    font-weight: 300;
    font-family: 'Roboto', sans-serif;

}
.sect2__text-lev{
    width: 100%;
}
.sect2__text-lev h1 {


    font-size: 70px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
    
}

.sect4-mb {
    display: none;
}

.sect4__mb-photo {
    display: none;
}

@media screen and (max-width:990px) {
    .sect4-mb {
        display: block;
    }

    .sect4-lg {
        display: none;
    }

    .sect2__bottom-title p {
        font-size: 20px;
    }

}

@media screen and (max-width:500px) {
    .sect4__mb-photo {
        display: block;
    }
    .sect2__text-lev h1{
        font-size: 40px;
        font-weight: 500;
        
    }

    .sect4__lg-photo {
        display: none;
    }

    .sect2__text-lev {
        padding-top: 10px;
    }

    .Sect4__title-lev {
        text-align: center;
        padding-top: 20px;
    }

    .sect2__bottom-title p {
        font-size: 16px;

    }

    .sect2__bottom-title {
        width: 100%;
    }

}