.section5 {
    height: 130vh;
    position: relative;
}

.sect5_bg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: -51px;

}

.sect5_bg img {
    width: 100%;
    height: 100%;
}

.sect5 {
    height: 130vh;
}

.sect5 p {
    padding-top: 170px;
    margin: auto;
    width: 300px;
    color: #66483e;
}

.sect5_bg img:nth-child(2) {
    display: none;
}


@media screen and (max-width:700px) {

    .sect5_bg img:nth-child(1) {
        display: none;
    }
    .sect5_bg img:nth-child(2) {
        display: block;
    }
    .sect5 p {
        padding: 0;
        text-align: center;
        width: 65%;
        font-size: 14px;
        line-height: 17px;
    }
}