.Section3 {
    position: relative;
    top: -31px;
}

.sect3__bg {
    position: absolute;
    z-index: -1;
    width: 100%;
}

.sect3 {
    height: 130vh;
}

.sect3_bgphoto {

    width: 100%;
    height: 130vh;
}
.sect2__title h1{
    font-size: 70px;
    text-align: center;
 
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;

    
    
}
.sect2__title {
    width: 100%;

}


.sect2__title h5 {
    margin: auto;
    width: 98%;
    padding-top: 15px;
    color: white;
    font-weight: 300;
    font-size: 30px;
    text-align: center;
}

.sect2__text-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    width: 80%;
}

.sect3__text1 h3 {
    color: #66483e;

    font-weight: 300;

}

.sect3__text2 h3 {
    color: #66483e;

    font-weight: 300;

}

.sect3_bgphoto2 {
    display: none;

}

@media screen and (max-width:990px) {
    .sect2__title h5 {
        display: none;
    }

    .sect3_bgphoto {
        display: none;
    }
    
    .sect3_bgphoto{
        display: none;
    }
    .sect3_bgphoto2 {
        display: block;
        width: 100%;
        height: 130vh;

    }
    .sect3_bgphoto{
        display: none;
    }
    .sect2__title h1{
        font-size: 35px;
        width: 100%;
        text-align: center;
        background: rgb(255,233,176);
        background: linear-gradient(90deg, rgba(255,233,176,1) 0%, rgba(250,196,101,1) 30%, rgba(254,241,208,1) 50%, rgba(250,196,101,1) 73%, rgba(255,233,176,1) 100%);
        background-clip: text;
        color: transparent;
        font-family: 'Open Sans', sans-serif;
        font-weight: 500;
        padding-top: 20px;
        
    }
}