.section12 {
    background-color: black;
    height: 100%;
    position: relative;
    top: -90px;
    padding: 50px 0px 40px 0px;

}

.sect12__title {
    margin: auto;
    width: 100%;
    text-align: center;
}

.sect12__title img {
    width: 100%;
}

.sect12__title p {
    padding-top: 30px;
    color: #d2cdca;
    font-weight: 300;

    font-size: 20px;
    font-family: 'Roboto' sans-serif;
}

.sect12__title {
    width: 100%;
}

.sect12__title h1 {

    font-size: 70px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 500;
    text-transform: uppercase;
}

.sect12__photo {
    width: 50%;
    margin: auto;

}

.sect12__title img {
    padding-top: 40px;
    width: 100%;
}

.sect12__mbimg {
    display: none;
}

@media screen and (max-width:900px) {
    .sect12__title h1 {

        font-size: 50px;
    }

    .sect12__title p {

        font-size: 18px;
    }


}

@media screen and (max-width:500px) {
    .sect12__title p {
        font-size: 15px;
        padding-top: 50px;

    }
    .sect12__title h1 {

        font-size: 30px;
    }

    .sect12__lgimg {
        display: none;
    }

    .sect12__mbimg {
        display: block;
    }

    .sect12__photo {
        width: 100%;
    }

    .section12 {
        padding: 30px 0px 40px 0px;

    }
}