.footer__logo1,
.footer__logo2,
.footer_text-future {
  display: flex;
  justify-content: center;
}
.footer__logo1{
  display: block;
}
.footer_contact{
  padding-top: 40px;
}
.site-footer {
  background-color: black;
  padding: 45px 0px;
  font-size: 15px;
  line-height: 24px;
  color: #737373;
}
.footer_contact{
  text-align: start;
}
.footer__logo1 img,
.footer__logo2 img,
.footer_text-future img {
  max-width: 200px;
}

.footer__text-phantom{
  padding-top: 40px;
  align-items: center;
}
.footer__email {


  color: #cfac79;
  font-size: 17px;
  letter-spacing: 2px;
}

.footer__text-nomer {

  color: #cfac79;
  font-size: 17px;
  letter-spacing: 2px;
  font-weight: 600;
}
.footer_text-future{
  display: flex;
  justify-content: center;
}
.footer_item-box{
  display: block;
  justify-content: center;
  align-items: center;
}

.footer__item{
  width: 230px;
  padding-top: 40px;
  margin: auto;
}
.footer__item img{
  width: 100%;
}

.osobenos{
  padding-left: 40px;
}

@media screen and (max-width:500px) {

  .footer__text-phantom{
    display: block;
  }
  .osobenos{
    padding: 0;
  }
  .footer__common > div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .footer_contact{
    padding-top: 10px;
  }
  .footer__logo2{
    padding-top: 50px;
  }

  .footer__text-osob img{
    align-items: center;

  }
  .footer__text-phantom{

    width: 100%;
    margin: auto;
  }
  .footer__text-osob{
    text-align: center;
    width: 100%;
    margin: auto;
    padding-top: 40px;
  }
  .footer_item-box{
    padding-top: 70px;
  }
  .footer__item{
    padding-top: 20px;
  }
}