.section8 {
    position: relative;
    top: -70px;
    width: 100%;
}

.sect8__bg {
    width: 100%;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
}

.sect8__title img {
    width: 100%;

}

.sect8__title {
    margin: auto;
    width: 60%;
}

.sect8__bg img {
    width: 100%;
    height: 100%;

}

.sect8__title {
    text-align: center;
    padding-top: 50px;
}

.sect8__video {
    width: 400px;
}

.sect8__card-video {
    padding-top: 50px;
}

.sect8__box {
    display: block;
}
.sect8__box h3{
    font-size: 34px;
}
.Sect8__cards2{
    width: 100%;
}

.sect8__box-text {
    color: #cdb6a7;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
    font-size: 40px;
    text-align: center;
}

.Sect8__cards2 {
    padding-top: 50px;
}
.sect8__title{
    width: 100%;
}
.sect8__title h1{
    
    font-size: 100px;
    text-align: center;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
}
@media screen and (max-width:900px) {
    .sect8__title h1{
        font-size: 55px;
    }
}

@media screen and (max-width:500px) {
    .sect8__title img {
        width: 100%;
    }

    .sect8__video {
        width:100%;
    }
    .sect8__box-text{
        font-size: 25px;
    }
    .sect8__box{
        padding-top: 40px;
    }
    .kopresor{
        padding-bottom: 30px;
    }
    .sect8__title h1{
        font-size: 35px;
        width: 100%;
    }
}