@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap');

.navbar {
    height: 125px;
}

.navBg {
    background-color: black;
}

.Navbar__text li {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 25px;
}

.nav {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-toggler-icon {
    color: rgb(203, 165, 113);

}

.Navbar_text-rol {
    color: rgb(203, 165, 113);
    color: linear-gradient(3deg, rgba(203, 165, 113, 1) 0%, rgba(235, 168, 40, 1) 100%);
    font-size: 12px;
    font-family: 'Open Sans', sans-serif;
}

.Navbar__textphoto {
    width: 100%;
}

.Navbar__email {
    width: 100%;
}

.Navbar__colonka3-text {
    width: 100%;
}

.Navbar__text-nomer {
    width: 100%;
}

.Navbar__button {
    display: flex;
    align-items: center;
    padding: 7px 50px;
    background-color: black;
    border-radius: 20px;
    border: 1px #cfac79 solid;
    transition: 500ms ease;

}

.Navbar__button:hover {
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.935);
    border-radius: 20px;
    border: 1px #cfac79 solid;

}

.Navbar__textphoto {
    width: 100%;
}

.Navbar__h5 {
    padding-left: 8px;
    padding-top: 5px;
    color: #cfac79;
    font-size: 17px;
    letter-spacing: 2px;
}

.Navbar__btn-zakas {
    display: flex;
    align-items: center;
    width: 100%;
}

.Navbar__items-korzina {
    border: 1px #cfac79 solid;
    border-radius: 50%;
    padding: 10px;
    transition: 500ms ease;

}

.Navbar__email {

    color: #cfac79;
    font-size: 17px;
    letter-spacing: 2px;
}

.Navbar__items-contact {
    display: flex;
    align-items: center;
    gap: 10px;
}

.Navbar__text-nomer {
    color: #cfac79;
    font-size: 17px;
    letter-spacing: 2px;
    font-weight: 600;
}

.Navbar__text-nomer {
    font-weight: 900;
}

.nav {
    display: flex;
    align-items: center;
}

.Navbar__items-korzina:hover {
    background-color: #fff;
}

.Navbar__logo {
    width: 100%;
}

@media screen and (max-width: 1260px) and (min-width:991px) {
    .Navbar__logo-body {
        width: 13%;
    }

    .Navbar__text li {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 15px;
    }


    .Navbar__button {
        padding: 7px 30px;
    }

    .Navbar__email {

        font-size: 15px;
    }

    .Navbar__text-nomer {

        font-size: 15px;
    }


}

.navbar-toggler-icon {
    background-image: url('https://i.ibb.co/0CfBsbQ/palki.png');
    width: 50px;
    height: 50px;
}


@media screen and (max-width:991px) {

    .colas {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    .collapse {
        position: absolute;
        background-color: black;
        width: 100%;
        top: 120px;
        z-index: 99;
    }

    .Navbar__items-korzina {
        display: none;
    }
    .nav-item{
        padding-top: 20px;
    }
    .Navbar__colonka3-text{
        padding-top: 30px;
        padding-bottom: 30px;
        padding-left: 30px;
    }
}

.modal-container {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 30%;
}

.modal-content input {
    border: 1px solid lightgray;
    padding: 5px;
    margin-bottom: 10px;
    width: 100%;

    font-size: 18px;
    padding: 10px;
    
}
.modal-content h3{
    padding-bottom: 20px;
}

.modal-content button {
    background-color: #007bff;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
}


.modal-content button.submitted {
    background-color: rgb(243, 243, 243);
    border: 1px rgb(222, 222, 222) solid;
    color: black;
}
