.section2 {

    background-color: black;
    position: relative;
    top: -15px;
    z-index: -2;
}
.section2-backtround img {
    position: absolute;
    z-index: -1;
}

.bg-sect2 {
    padding-top: 150px;

    width: 100%;
}


.mobile__sect2 {
    display: none;
}

.Sect2__title {
    color: white;
    display: flex;
    justify-content: center;
    width: 100%;
}

.Sect2__title img {
    width: 100%;

}

.Sect2__title h1 {
    font-size: 70px;
    text-align: center;
    width: 100%;

    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;

}

.Sect2__title {
    width: 100%;

}

.Sect2_box2 {
    width: 500px;
}

.Sect2_box2 img {
    width: 100%;

}

.Sect2__boxses {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.Sect2__box1 img {
    width: 340px;

}

.Sect2__boxses {
    padding-top: 80px;
    padding-left: 50px;
}

.Sect2_box2 {
    padding-top: 80px;
}

.Sect2__text {
    color: white;
    text-align: end;
}

.Sect2__text {
    margin: auto;
    width: 75%;

}

.Sect2__text h2 {
    color: #cdb6a7;
    font-weight: 300;
    font-family: 'Roboto Condensed', sans-serif;

    letter-spacing: 2px;


}

.Sect2__subtitle {
    color: #c4bfbd;
    text-align: center;
}

.Sect2__subtitle p {
    font-size: 18px;
    padding-top: 10px;
    padding-bottom: 20px;
    font-family: 'Roboto ', sans-serif;


}
.bg2-sect2{
    display: none;
}

@media screen and (max-width:990px) {

    .Sect2__box1 {
        width: 50%;
    }

    .Sect2_box2 {
        width: 50%;
    }

    .Sect2_box2 img {
        width: 100%;
    }

    .Sect2__box1 img {
        width: 100%;

    }

    .Sect2__subtitle p {
        font-size: 14px;
    }

    .Sect2__boxses {
        padding-top: 20px;
    }

    .lg__sect2 {
        display: none;
    }

    .mobile__sect2 {
        display: block;
        width: 100%;

    }

    .Sect2__text {
        text-align: center;
        padding-top: 30px;
    }

    .Sect2__title {
        width: 90%;
        margin: auto;
    }

    /* .section2-backtround img {
        padding-top: 150px;
    }

    .section2-backtround img {
        top: 270px;
    } */

    .Sect2__text h2 {
        font-size: 20px;

    }

    .Sect2__subtitle {
        padding: 0;
    }

    .Sect2__title h1 {
        width: 100%;
        font-size: 40px;

    }

    .Sect2__title h1 {
        font-size: 20px;
    }

    .Sect2__boxses {
        width: 100%;
    }
    .bg2-sect2 {
        display: block;
        padding-top: 50px;
        width: 100%;
    }
    .bg-sect2{
        display: none;
    }
}
@media screen and (max-width:500px) {
    

    .Sect2__subtitle p {
        font-size: 13px;
    }
    .bg2-sect2{
        padding-top: 30px;
    }

    .sect2__minu {
        padding-top: 10px;
    }
}