.section9{
    width: 100%;
    position: relative;
    top: -70px;
    height: 137vh;
}
.sect9__bg{
    position: absolute;
    z-index: -1;
    width: 100%;
}
.sect9__bg img{
    width: 100%;
}
.sect9__cards{
    width: 100%;
}
.sect9__cards img{
    width: 100%;
}
.sect9__bg img:nth-child(2){
    display: none;
}
.sect9__cards-item{
    display: flex;
    justify-content: center;
    align-items: center;
}
.sect9__text-item1{
    width: 50%;
}
.sect9__bg img{
    height: 100%;
}
.sect9__text-item1 h1{
    text-align: end;
    font-size: 70px;
    text-align: end;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    text-transform: uppercase;
}
.sect9__text-item2 h1{
    text-align: end;
    font-size: 50px;
    text-align: start;
    background: rgb(255, 233, 176);
    background: linear-gradient(90deg, rgba(255, 233, 176, 1) 0%, rgba(250, 196, 101, 1) 30%, rgba(254, 241, 208, 1) 50%, rgba(250, 196, 101, 1) 73%, rgba(255, 233, 176, 1) 100%);
    background-clip: text;
    color: transparent;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
}
.sect9__cards-text{
    width: 100%;
    padding-bottom: 50px;
}
.sect9__cards-text img{
    width: 100%;
}
.sect9__palka img{
    height: 20px;
    width: 10px;
}
.stick {
    margin: 0px 30px;
  width: 2px; /*   */
  height: 140px; /*   */
  background-color: white; /*   () */
}
.sect9__cards-text{
    margin: auto;
    width: 95%;
}
.sect9__card-text-left{
    width: 40%;
}
.sect9__card-text-item1 p{
    width: 60%;
    color: white;
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    font-weight: 600;


}
.sect9__common-crovat{
    width: 50%;
    margin: auto;
    display: flex;
    justify-content: center;
    padding-top: 190px;
}
.sect9__krovat{
    
    width: 30%;
    position: absolute;
    z-index: -1;
    top: 280px;
    
}
.sect9__card-text-item1{
    display: block;
}
.sect9__krovat img{
    width: 100%;
    margin: auto;
}

@media screen and (max-width:500px) {

    .sect9__bg img:nth-child(1){
        display: none;

    }
    .sect9__bg img:nth-child(2){
        display: block;

    }
}